}

.footer {
  background-color: #3c3c3b;
}
.footer__contenedor {
  padding: 2rem 0;
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  text-align: center;
  justify-content: space-between;
  border-bottom: 1px solid #f6f6f6;
}
@media (min-width: 768px) {
  .footer__contenedor {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__h3 {
  color: #f6f6f6;
}
.footer__info {
  display: flex;
  flex-direction: column;
}
.footer__servicios {
  font-weight: bold;
}
.footer__asesorias {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer__asesorias i {
  font-size: 3rem;
}
.footer__lenguajes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.footer__copy {
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 0;
}
.footer__span {
  color: #007df4;
}
.footer__logo {
  font-size: 2rem;
  color: #1a1b15;
  font-weight: bold;
}
.footer__div {
  display: flex;
  color: #1a1b15;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.header {
  background-color: #f6f6f6;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999999;
}
.header__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .header__contenido {
    flex-direction: row;
  }
}
.header__logo {
  color: #1a1b15;
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: none;
  }
}
.header__nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
}
.header__nav a {
  color: #007df4;
  font-weight: bold;
}
@media (min-width: 768px) {
  .header__nav {
    flex-direction: row;
    padding: 0;
  }
}
.header__contacto {
  background-color: #114089;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 1.5rem;
  color: #FFFFFF !important;
}
.header__contacto:hover {
  background-color: #0b2b5c;
  cursor: pointer;
}
.header__span {
  color: #007df4;
}

.icono-whats {
  font-size: 7rem;
  color: #02db02;
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 2rem;
}

.intro {
  width: min(95%, 140rem);
  margin: 0 auto;
  margin-top: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .intro {
    padding: 4rem 0;
  }
}
@media (min-width: 768px) {
  .intro {
    flex-direction: row;
    margin-top: 10rem;
  }
}
.intro__h1 {
  line-height: 1.6;
}
.intro__p {
  font-size: 1.9rem;
