}
.intro__p a {
  color: #007df4;
  font-size: 2rem;
  font-weight: bold;
}
.intro__enlaces {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .intro__enlaces {
    margin-bottom: 0;
  }
}
.intro__enlaces a {
  border-radius: 1rem;
}
.intro__empieza {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.intro__empieza:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.intro__proyectos {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
}
.intro__proyectos:hover {
  background-color: black;
  cursor: pointer;
}
.intro__img {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  -webkit-box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  height: max-content;
}

.main {
  margin-top: 3rem;
  padding: 2rem 0 3rem 0;
  background-color: #1a1b15;
}
.main__heading {
  color: #FFFFFF;
  text-align: center;
}

.servicios {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .servicios {
    grid-template-columns: repeat(4, 1fr);
  }
}

.servicio {
  text-align: center;
  display: flex;
  flex-direction: column;
  transition: all 300ms;
  background-color: #f6f6f6;
  border-radius: 1rem;
  overflow: hidden;
  cursor: pointer;
}
.servicio__img {
  flex-basis: 60%;
}
.servicio__img img {
  height: 100%;
  object-fit: cover;
}
.servicio__info {
  flex-basis: 40%;
  padding: 1rem;
}
.servicio:hover {
  background-color: #f6f6f6;
  transform: scale(1.1);
}

.estudio {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 4rem;
  width: min(95%, 140rem);
  margin: 0 auto;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .estudio {
    flex-direction: row;
  }
}
.estudio__img {
  flex-basis: 40%;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  -webkit-box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 7px 10px 5px -2px rgba(0, 0, 0, 0.57);
  height: fit-content;
}
.estudio__info {
  flex-basis: 60%;
  text-align: center;
}
.estudio__heading {
  color: #007df4;
}
.estudio__contacto {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 1rem;
  width: 50%;
}
.estudio__contacto:hover {
  background-color: black;
  cursor: pointer;
}

.proyectos {
  background-color: #1a1b15;
  padding: 2rem 0;
}
.proyectos__heading {
  color: #FFFFFF;
}

.proyecto img {
  min-height: 40rem;
  max-height: 60rem;
  object-fit: cover;
}

.slider {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.contacto {
  width: min(95%, 140rem);
  margin: 0 auto;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contacto {
    text-align: unset;
  }
}
.contacto h3 {
  color: #007df4;
}
.contacto p {
  line-height: 2;
}
.contacto p span {
  font-weight: bold;
}
@media (min-width: 768px) {
  .contacto {
    flex-direction: row;
  }
  .contacto__info {
    flex-basis: 40%;
  }
  .contacto__form {
    flex-basis: 60%;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form.ocultar {
  display: none;
}
.form__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form__campo label {
  font-weight: bold;
}
.form__campo input {
  padding: 1rem;
  font-size: 2rem;
  color: #1a1b15;
  border-radius: 1rem;
  border: 1px solid #1E293B;
}
.form__campo select {
  padding: 1rem;
  font-size: 2rem;
  color: #1a1b15;
  border-radius: 1rem;
  border: 1px solid #1E293B;
}
.form__submit {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 1rem;
}
.form__submit:hover {
  background-color: black;
  cursor: pointer;
}

.sk-cube-grid {
  width: 40px;
  height: 40px;
  margin: 100px auto;
}

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #333;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}

.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}

.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}

.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}

.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}

.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

@-webkit-keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
  }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
  }
}
@keyframes sk-cubeGridScaleDelay {
  0%, 70%, 100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1);
  }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1);
  }
}
.spinner {
  display: none;
}
.spinner.activo {
  display: block;
}